import { COLOR_LIST } from '../../utils/config.js';

export default {
	data() {
		return {
			list: [],
			colorList: COLOR_LIST
		};
	},
	computed: {
		sortlist() {
			let newList = [...this.list];
			return newList.sort((a, b) => (a.date < b.date ? 1 : -1));
		}
	},
	onLoad() {
		this.loadData();
	},
	mounted() {
		console.log('mounted');
	},
	onShow() {
		this.loadData();
	},
	// 下拉刷新
	onPullDownRefresh() {
		setTimeout(() => {
			uni.showToast({
				title: '刷新数据完成',
				icon: 'none'
			});
			uni.stopPullDownRefresh();
		}, 1000);
	},
	// 右上角按钮点击
	onNavigationBarButtonTap(e) {
		uni.navigateTo({
			url: '/pages/index/add'
		});
	},
	methods: {
		loadData(cb) {
			this.list = [...this.$store.state.home.list];
		}
	}
};
